import uniStarterConfig from '@/uni-starter.config.js';
//









const db = uniCloud.database()
export default async function() {
	const debug = uniStarterConfig.debug;

	// uniStarterConfiggetApp().globalData.config
	setTimeout(() => {
		getApp({
			allowDefault: true
		}).globalData.config = uniStarterConfig;
	}, 1)


	// appVersionapp
	initAppVersion();

	//clientDB
	function onDBError({
		code, // https://uniapp.dcloud.net.cn/uniCloud/clientdb?id=returnvalue
		message
	}) {
		console.log('onDBError', {
			code,
			message
		});
		// 
		console.error(code, message);
	}
	// clientDB
	db.on('error', onDBError)


	//
	uniCloud.interceptObject({
		async invoke({
			objectName, // 
			methodName, // 
			params // 
		}) {
			// console.log('interceptObject',{
			// 	objectName, // 
			// 	methodName, // 
			// 	params // 
			// });
			if(objectName == "uni-id-co" && (methodName.includes('loginBy') ||  ['login','registerUser'].includes(methodName) )){
				console.log('');
				params[0].inviteCode = await new Promise((callBack) => {
					uni.getClipboardData({
						success: function(res) {
							console.log(''+res.data);
							if (res.data.slice(0, 18) == 'uniInvitationCode:') {
								let uniInvitationCode = res.data.slice(18, 38)
								console.log(',code' + uniInvitationCode);
								// uni.showModal({
								// 	content: ',code'+uniInvitationCode,
								// 	showCancel: false
								// });
								callBack(uniInvitationCode)
								//id myInviteCode
							} else {
								callBack()
							}
						},
						fail() {
							console.log('error--');
							callBack()
						},
						complete() {

							uni.hideToast()

						}
					});
				})
				// console.log(params);
			}
			// console.log(params);
		},
		success(e) {
			console.log(e);
		},
		complete() {

		},
		fail(e){
			console.error(e);
			// if (debug) {
			// 	uni.showModal({
			// 		content: JSON.stringify(e),
			// 		showCancel: false
			// 	});
			// }else{
			// 	uni.showToast({
			// 		title: '',
			// 		icon:'error'
			// 	});
			// }
		}
	})























}
